import React, { useEffect, useState } from 'react'

const NavBar = (props) => {
  const navItems = props.navItems;
  const navContents = navItems.map((item) =>
    <li className="nav-item" key={item.name}>
      <a className="nav-link">{item.name}</a>
    </li>
  );
  const [withColor, setColor] = useState('transparent');

  const toggleColor = () => {
    if (window.pageYOffset > 100) {
      setColor('#C7F1ED');
    } else {
      setColor('transparent');
    }
  };
  useEffect(() => {
    window.addEventListener("scroll", toggleColor);
    return () => {
      window.removeEventListener("scroll", toggleColor);
    };
  }, []);
  return (
    <nav className="navbar navbar-expand-lg navbar-light fixed-top oramaNav" style={{ backgroundColor: `${withColor}` }}>
      <div className="container-fluid">
        <a className="navbar-brand oramaNav-icon" href="#"><img src="/oramaWeb/imgs/oramadick.png" width="40" height="40" style={{ right: "50%" }} /></a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarColor03" aria-controls="navbarColor03" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse oramaNav-menu" id="navbarColor03">
          <ul className="navbar-nav ms-auto">
            {navContents}
            <li className="nav-item dropdown">
              <a className="nav-link dropdown-toggle" data-bs-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">Dropdown</a>

              <div className="dropdown-menu sub-nav">
                {/* <div class="sub-nav-col left"> */}
                <a className="dropdown-item sub-nav-box" href="#">Action</a>
                <a className="dropdown-item sub-nav-box" href="#">Another action</a>
                {/* </div> */}
                {/* <div class="sub-nav-col right"> */}
                <a className="dropdown-item sub-nav-box" href="#">Something else here</a>

                <a className="dropdown-item sub-nav-box" href="#">Separated link</a>
                {/* </div> */}
              </div>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  )
}

export default NavBar
